import React, { ReactNode, useRef, MouseEvent, useCallback } from 'react'
// import { BackdropTransition, SlideoutTransition } from '@primer/components/AlertTransition'
import alert from '@primer/utils/alert'
import { motion, AnimatePresence, MotionStyle } from 'framer-motion'
import { SlideoutResolveFn } from '@primer/types'
import clsx from 'clsx'

const sizes = {
	sm: 'max-w-sm',
	md: 'max-w-md',
	lg: 'max-w-lg',
	xl: 'max-w-xl',
	'2xl': 'max-w-2xl',
}

export type SizeValue = keyof typeof sizes

interface SlideoutProps<T> {
	resolve: SlideoutResolveFn<T>
	close: () => void
	dismissable?: boolean
	size?: SizeValue
	mode?: 'dark' | ''
	children: (close: SlideoutResolveFn<T>) => ReactNode
}

function Slideout<T>({ close, resolve, children, size = 'md', mode = '', dismissable = true }: SlideoutProps<T>) {
	const [open, setOpen] = React.useState(true)

	const closeSlideout = useCallback(
		(x) => {
			resolve(x)

			setOpen(false)
			setTimeout(() => {
				close()
			}, 500)
		},
		[close, resolve]
	)

	const backdropRef = useRef<HTMLDivElement>(null)

	return (
		<AnimatePresence>
			{open && (
				<motion.div
					style={{ originY: 0 } as MotionStyle}
					variants={{
						hidden: { opacity: 0 },
						visible: { opacity: 1 },
					}}
					transition={{
						type: 'tween',
						ease: 'easeOut',
						duration: 0.2,
					}}
					initial="hidden"
					animate="visible"
					exit="hidden"
				>
					<div
						className={clsx('fixed z-40 inset-0 overflow-hidden', mode)}
						onClick={(e: MouseEvent) => {
							if (e.target === backdropRef.current) {
								dismissable && closeSlideout(false)
							}
						}}
						ref={backdropRef}
					>
						<div className="absolute z-40 inset-0 overflow-hidden">
							<div
								className="absolute z-40 inset-0 bg-black bg-opacity-50 transition-opacity backdrop-filter backdrop-grayscale"
								onClick={() => closeSlideout(false)}
							/>
							<section className={clsx('absolute z-40 inset-y-0 right-0 w-full flex', sizes[size])}>
								<motion.div
									className="pointer-events-none flex-1"
									variants={{
										hidden: { x: '100%' },
										visible: { x: '0%' },
									}}
									transition={{
										type: 'tween',
										ease: 'easeOut',
										duration: 0.2,
									}}
									initial="hidden"
									animate="visible"
									exit="hidden"
								>
									<div className="pointer-events-auto h-full w-full">{children(closeSlideout)}</div>
								</motion.div>
							</section>
						</div>
					</div>
				</motion.div>
			)}
		</AnimatePresence>
	)
}

export default Slideout

interface slideoutProps<T> {
	dismissable?: boolean
	size?: SizeValue
	mode: 'dark' | ''
	children: (close: SlideoutResolveFn<T>) => ReactNode
}

export function slideout<T = boolean>({ dismissable, size, mode = '', children }: slideoutProps<T>) {
	return alert<T>(({ resolve, close }) => {
		return (
			<Slideout
				resolve={resolve}
				close={close}
				children={children}
				dismissable={dismissable}
				size={size}
				mode={mode}
			/>
		)
	})
}
