import React, { FC, useState } from 'react'
import { CardWrapper } from '@primer/components/Card'
import PageHeading from '@primer/components/PageHeading'
import PageContent from '@primer/components/PageContent'
import { Link, RouteComponentProps } from '@reach/router'
import db from 'db'
import Table, { TableSchema } from '@primer/components/Table'
import { EntityFieldModel, EntityModel } from 'types/models'
import { AdjustmentsIcon, CheckIcon, DotsHorizontalIcon, PencilIcon, TrashIcon } from '@heroicons/react/outline'
import { confirm } from '@primer/alerts'
import { getEntityFieldValue, sortValuesByLatest } from 'utils/funcs'
import Actions from '@primer/components/Actions'

const Entities: FC<RouteComponentProps<{ entityTypeId: string }>> = ({ entityTypeId }) => {
	const entityType = db.entityTypes.find(entityTypeId)

	const fields = db.entityFields.all((x) => x.entityTypeId === entityTypeId)

	const getValue = (entity: EntityModel, field?: EntityFieldModel) =>
		db.entityValues
			.all((y) => y.entityId === entity.id && y.entityFieldId === field?.id)
			.sort(sortValuesByLatest)[0]

	const [visibleFields, setVisibleFields] = useState(fields)

	const schema: TableSchema<EntityModel> = {
		cols: fields
			.filter((x) => visibleFields.includes(x))
			.map((field, i) => ({
				title: field.name,
				value: (x) => {
					let val = getEntityFieldValue(getValue(x, field))
					return i === 0 ? (
						<Link to={x.id} className="anchor">
							{val || '-'}
						</Link>
					) : (
						val || '-'
					)
				},
			})),
		actions: (x) => [
			{
				icon: <DotsHorizontalIcon className="w-5 h-5" />,
				intent: 'menu',
				actions: [
					[
						{
							title: `Edit ${entityType?.name}`,
							icon: <PencilIcon className="w-5 h-5" />,
							to: `/${entityType?.key}/${x.id}/edit`,
						},
					],
					[
						{
							title: `Delete ${entityType?.name}`,
							icon: <TrashIcon className="w-5 h-5" />,
							intent: 'danger',
							onClick: () => {
								confirm({
									title: 'Are you sure?',
									message: 'This cannot be undone',
									onAccept: () => {
										db.entities.remove(x.id)
									},
								})
							},
						},
					],
				],
			},
		],
	}

	return (
		<>
			<PageHeading
				title={entityType?.plural || entityType?.name || '-'}
				actions={[
					{
						title: `Create ${entityType?.name}`,
						intent: 'primary',
						to: `/${entityType?.key}/create`,
					},
				]}
			/>

			<PageContent>
				<CardWrapper className="overflow-hidden">
					<div className="relative">
						<div className="absolute top-0 right-0 h-[42px] flex items-center mr-1">
							<Actions
								actions={{
									icon: <AdjustmentsIcon className="w-4 h-4" />,
									intent: 'menu',
									actions: fields.map((x) => ({
										title: x.name,
										icon: visibleFields.includes(x) ? (
											<CheckIcon className="w-4 h-4" />
										) : (
											<div className="w-4 h-4" />
										),
										iconSide: 'left',
										disableDismiss: true,
										onClick: () =>
											setVisibleFields((f) =>
												visibleFields.includes(x) ? f.filter((y) => y !== x) : [...f, x]
											),
									})),
								}}
							/>
						</div>

						<Table
							schema={schema}
							items={db.entities.all((x) => x.entityTypeId === entityType?.id)}
							className="-my-px"
						/>
					</div>
				</CardWrapper>
			</PageContent>
		</>
	)
}

export default Entities
