import {
	EntityTypeModel,
	EntityFieldModel,
	EntityActionModel,
	EntityConnectionModel,
	Unsaved,
	isUnsaved,
	NotificationModel,
	TemplateModel,
	RoleModel,
	EntityActionPermissionModel,
	EntityConnectionPermissionModel,
	EntityFieldPermissionModel,
	EntityTypePermissionModel,
	EntityModel,
	EntityValueModel,
	SiteSettings,
} from 'types/models'
import { v4 as uuid } from 'uuid'

interface Storage {
	siteSettings: SiteSettings
	entities: EntityModel[]
	entityValues: EntityValueModel[]
	entityTypes: EntityTypeModel[]
	entityTypePermissions: EntityTypePermissionModel[]
	entityFields: EntityFieldModel[]
	entityFieldPermissions: EntityFieldPermissionModel[]
	entityActions: EntityActionModel[]
	entityActionPermissions: EntityActionPermissionModel[]
	entityConnections: EntityConnectionModel[]
	entityConnectionPermissions: EntityConnectionPermissionModel[]
	notifications: NotificationModel[]
	templates: TemplateModel[]
	roles: RoleModel[]
}

const initialSettings: SiteSettings = { title: 'NZDRC2', layout: 'sidebar' }

const storage: Storage = {
	siteSettings: initialSettings,
	entities: [],
	entityValues: [],
	entityTypes: [],
	entityTypePermissions: [],
	entityFields: [],
	entityFieldPermissions: [],
	entityActions: [],
	entityActionPermissions: [],
	entityConnections: [],
	entityConnectionPermissions: [],
	notifications: [],
	templates: [],
	roles: [],
}

export const renderContainer = {
	current: () => {},
}

const dataActions = <T extends Storage[K][0], K extends keyof Omit<Storage, 'siteSettings'>>(prop: K) => ({
	all: (filter?: (x: T) => boolean): T[] => {
		if (filter && storage[prop]) return Array.prototype.filter.call(storage[prop], filter)
		// @ts-ignore
		return storage[prop] || []
	},
	find: (id?: string | ((x: T) => boolean)): T | undefined => {
		return Array.prototype.find.call(storage[prop], typeof id === 'function' ? id : (x) => x.id === id)
	},
	save: (data: T | Unsaved<T>) => {
		if (isUnsaved<T>(data)) {
			let x = {
				...data,
				id: uuid(),
				created: new Date().toISOString(),
			}
			Array.prototype.push.call(storage[prop], x)
			persist()
			renderContainer.current()
			return x
		} else {
			let added = false

			// @ts-ignore
			storage[prop] = storage[prop].map((x) => {
				// @ts-ignore
				if (x.id === data.id) {
					added = true
					return data
				}
				return x
			})

			if (!added) Array.prototype.push.call(storage[prop], data)

			persist()
			renderContainer.current()
			return data
		}
	},
	remove: (id: string) => {
		// @ts-ignore
		storage[prop] = storage[prop].filter((x) => x.id !== id)
		persist()
		renderContainer.current()
	},
})

const data = {
	siteSettings: {
		set: <K extends keyof SiteSettings>(key: K, val: SiteSettings[K]) => {
			storage.siteSettings[key] = val
			renderContainer.current()
			persist()
		},
		get: <K extends keyof SiteSettings>(key: K) => {
			return storage.siteSettings[key]
		},
	},
	entities: dataActions('entities'),
	entityValues: dataActions('entityValues'),
	entityTypes: dataActions('entityTypes'),
	entityTypePermissions: dataActions('entityTypePermissions'),
	entityFields: dataActions('entityFields'),
	entityFieldPermissions: dataActions('entityFieldPermissions'),
	entityActions: dataActions('entityActions'),
	entityActionPermissions: dataActions('entityActionPermissions'),
	entityConnections: dataActions('entityConnections'),
	entityConnectionPermissions: dataActions('entityConnectionPermissions'),
	notifications: dataActions('notifications'),
	templates: dataActions('templates'),
	roles: dataActions('roles'),
}

export const persist = () => localStorage.setItem('data', JSON.stringify(storage))

export const restore = () => {
	const data = JSON.parse(localStorage.getItem('data') || 'null')

	if (data) {
		storage.siteSettings = data.siteSettings || {}
		storage.entities = data.entities || []
		storage.entityValues = data.entityValues || []
		storage.entityTypes = data.entityTypes || []
		storage.entityTypePermissions = data.entityTypePermissions || []
		storage.entityFields = data.entityFields || []
		storage.entityFieldPermissions = data.entityFieldPermissions || []
		storage.entityActions = data.entityActions || []
		storage.entityActionPermissions = data.entityActionPermissions || []
		storage.entityConnections = data.entityConnections || []
		storage.entityConnectionPermissions = data.entityConnectionPermissions || []
		storage.notifications = data.notifications || []
		storage.templates = data.templates || []
		storage.roles = data.roles || []
	}
}

export const clear = () => {
	localStorage.removeItem('data')

	storage.siteSettings = initialSettings
	storage.entities = []
	storage.entityValues = []
	storage.entityTypes = []
	storage.entityTypePermissions = []
	storage.entityFields = []
	storage.entityFieldPermissions = []
	storage.entityActions = []
	storage.entityActionPermissions = []
	storage.entityConnections = []
	storage.entityConnectionPermissions = []
	storage.notifications = []
	storage.templates = []
	storage.roles = []
}

restore()

export default data
