import React from 'react'
import { CheckIcon, XIcon } from '@heroicons/react/outline'
import { format } from 'date-fns'
import db from 'db'
import { EntityValueModel } from 'types/models'

export const getEntityFieldValue = (val?: EntityValueModel) => {
	if (!val) return

	let field = db.entityFields.find(val.entityFieldId)

	switch (field?.type) {
		case 'string':
		case 'option':
			return val.stringValue
		case 'date':
			return val.dateValue ? format(val.dateValue, 'dd/MM/yyyy') : undefined
		case 'number':
			return val.numberValue
		case 'boolean':
			return val.booleanValue ? <CheckIcon className="w-4 h-4" /> : <XIcon className="w-4 h-4" />
	}
}

export const sortValuesByLatest = (a: EntityValueModel, b: EntityValueModel) => {
	if (a.created && b.created) return new Date(b.created).valueOf() - new Date(a.created).valueOf()
	return 0
}
