import { EntityActionModel, EntityTypeModel, EntityFieldModel, RoleModel } from 'types/models'
import { v4 as id } from 'uuid'
import db, { clear, persist } from './index'

export const seed = () => {
	clear()
	persist()

	db.siteSettings.set('title', 'NZDRC2')
	db.siteSettings.set('layout', 'sidebar')

	// ENTITY TYPES

	const userEntityType: EntityTypeModel = {
		id: id(),
		key: 'user',
		name: 'User',
		plural: 'Users',
		mainNav: true,
	}

	const caseEntityType: EntityTypeModel = {
		id: id(),
		key: 'case',
		name: 'Case',
		plural: 'Cases',
		mainNav: true,
	}

	const eventEntityType: EntityTypeModel = {
		id: id(),
		key: 'event',
		name: 'Event',
		plural: 'Events',
		mainNav: false,
	}

	db.entityTypes.save(userEntityType)
	db.entityTypes.save(caseEntityType)
	db.entityTypes.save(eventEntityType)

	// META TYPES

	// CASE

	const caseEntityFields: EntityFieldModel[] = [
		{
			id: id(),
			entityTypeId: caseEntityType.id,
			key: 'case-number',
			name: 'Case Number',
			type: 'string',
			options: [],
		},
		{
			id: id(),
			entityTypeId: caseEntityType.id,
			key: 'type',
			name: 'Type',
			type: 'option',
			options: ['Review', 'Mediation', 'Hybrid'],
			defaultValue: 'Review',
		},
		{
			id: id(),
			entityTypeId: caseEntityType.id,
			key: 'claim-number',
			name: 'Claim Number',
			type: 'string',
			options: [],
		},
		{
			id: id(),
			entityTypeId: caseEntityType.id,
			key: 'complexity',
			name: 'Complexity',
			type: 'option',
			options: ['Standard', 'Complex'],
		},
		{
			id: id(),
			entityTypeId: caseEntityType.id,
			key: 'closed-date',
			name: 'Closed Date',
			type: 'date',
			options: [],
		},
		{
			id: id(),
			entityTypeId: caseEntityType.id,
			key: 'status',
			name: 'Status',
			type: 'option',
			options: ['Pending', 'Open', 'Closed'],
		},
	]

	caseEntityFields.forEach(db.entityFields.save)

	// EVENTS

	const eventEntityFields: EntityFieldModel[] = [
		{
			id: id(),
			entityTypeId: eventEntityType.id,
			key: 'start-date',
			name: 'Start Date',
			type: 'date',
			options: [],
		},
		{
			id: id(),
			entityTypeId: eventEntityType.id,
			key: 'end-date',
			name: 'End Date',
			type: 'date',
			options: [],
		},
		{
			id: id(),
			entityTypeId: eventEntityType.id,
			key: 'type',
			name: 'Type',
			type: 'option',
			options: ['Review', 'Mediation', 'CMC'],
			defaultValue: 'CMC',
		},
		{
			id: id(),
			entityTypeId: eventEntityType.id,
			key: 'decision-date',
			name: 'Decision Date',
			type: 'date',
			options: [],
		},
		{
			id: id(),
			entityTypeId: eventEntityType.id,
			key: 'postponed',
			name: 'Postponed',
			type: 'boolean',
			options: [],
		},
	]

	eventEntityFields.forEach(db.entityFields.save)

	// USERS

	const userEntityFields: EntityFieldModel[] = [
		{
			id: id(),
			entityTypeId: userEntityType.id,
			key: 'first-name',
			name: 'First Name',
			type: 'string',
			options: [],
		},
		{
			id: id(),
			entityTypeId: userEntityType.id,
			key: 'last-name',
			name: 'Last Name',
			type: 'string',
			options: [],
		},
		{
			id: id(),
			entityTypeId: userEntityType.id,
			key: 'email',
			name: 'Email',
			type: 'string',
			options: [],
		},
		{
			id: id(),
			entityTypeId: userEntityType.id,
			key: 'phone',
			name: 'Phone',
			type: 'string',
			options: [],
		},
		{
			id: id(),
			entityTypeId: userEntityType.id,
			key: 'active',
			name: 'Active',
			type: 'boolean',
			options: [],
			defaultValue: 'true',
		},
	]

	userEntityFields.forEach(db.entityFields.save)

	// ACTIONS

	// CASE

	const caseActions: EntityActionModel[] = [
		{
			id: id(),
			entityTypeId: caseEntityType.id,
			key: 'close-case',
			name: 'Close Case',
			changes: [],
		},
	]

	caseActions.forEach(db.entityActions.save)

	// EVENTS

	const eventActions: EntityActionModel[] = []

	eventActions.forEach(db.entityActions.save)

	// CONNECTIONS

	db.entityConnections.save({
		id: id(),
		key: 'events',
		name: 'Events',
		entityTypeId: caseEntityType.id,
		relationId: eventEntityType.id,
		type: 'one-to-many',
	})

	db.entityConnections.save({
		id: id(),
		key: 'attendees',
		name: 'Attendees',
		entityTypeId: eventEntityType.id,
		relationId: userEntityType.id,
		type: 'one-to-many',
	})

	db.entityConnections.save({
		id: id(),
		key: 'applicant',
		name: 'Applicant',
		entityTypeId: userEntityType.id,
		relationId: caseEntityType.id,
		type: 'one-to-many',
	})

	// NOTIFICATIONS

	db.notifications.save({
		id: id(),
		name: 'Event Reminder 2 days',
		focus: eventEntityType.id,
		enabled: true,
		triggerType: 'date',
		triggerRepeat: 2,
		triggerEvery: '',
		triggerYearIn: '',
		triggerMonthOn: '',
		triggerWeekOn: '',
		triggerDayAt: '',
		triggerAction: '',
		obeyTriggerTiming: true,
		triggerTimingType: 'before',
		triggerTiming: 'day',
		triggerDate:
			db.entityFields.find((x) => x.entityTypeId === eventEntityType.id && x.key === 'start-date')?.id || '',
		ruleGroups: [
			{
				rules: [
					{
						entity: eventEntityType.id,
						field:
							db.entityFields.find((x) => x.entityTypeId === eventEntityType.id && x.key === 'postponed')
								?.id || '',
						operator: 'false',
						value: '',
					},
				],
			},
		],
		subject: 'Upcoming Conference',
		content:
			'<h1>Hello {{recipient-first-name}}</h1><p><br></p><p>You have a {{event-type}} coming up on {{event-start-date}}</p>',
	})

	db.templates.save({
		id: id(),
		name: 'Event Reminder 2 days',
		content:
			'<h1>Hello {{recipient-first-name}}</h1><p><br></p><p>You have a {{event-type}} coming up on {{event-start-date}}</p>',
	})

	const adminRole = db.roles.save({
		id: id(),
		name: 'Administrator',
	})

	const caseManagerRole = db.roles.save({
		id: id(),
		name: 'Case Manager',
	})

	const reviewerRole = db.roles.save({
		id: id(),
		name: 'Reviewer',
	})

	const applicantRole = db.roles.save({
		id: id(),
		name: 'Applicant',
	})

	;([adminRole, caseManagerRole, reviewerRole] as RoleModel[]).forEach((role) => {
		db.entityTypes.all().forEach((entityType) => {
			db.entityTypePermissions.save({
				id: id(),
				entityTypeId: entityType.id,
				roleId: role.id,
				view: true,
				modify: true,
				remove: true,
			})

			db.entityFields
				.all((x) => x.entityTypeId === entityType.id)
				.forEach((entityField) => {
					db.entityFieldPermissions.save({
						id: id(),
						entityFieldId: entityField.id,
						roleId: role.id,
						view: true,
						modify: true,
					})
				})

			db.entityActions
				.all((x) => x.entityTypeId === entityType.id)
				.forEach((entityAction) => {
					db.entityActionPermissions.save({
						id: id(),
						entityActionId: entityAction.id,
						roleId: role.id,
						perform: true,
					})
				})

			db.entityConnections
				.all((x) => x.entityTypeId === entityType.id)
				.forEach((entityConnection) => {
					db.entityConnectionPermissions.save({
						id: id(),
						entityConnectionId: entityConnection.id,
						roleId: role.id,
						view: true,
						modify: true,
						remove: true,
					})
				})
		})
	})

	db.entityTypes.all().forEach((entityType) => {
		db.entityTypePermissions.save({
			id: id(),
			entityTypeId: entityType.id,
			roleId: applicantRole.id,
			view: true,
			modify: false,
			remove: false,
		})

		db.entityFields
			.all((x) => x.entityTypeId === entityType.id)
			.forEach((entityField) => {
				db.entityFieldPermissions.save({
					id: id(),
					entityFieldId: entityField.id,
					roleId: applicantRole.id,
					view: true,
					modify: false,
				})
			})

		db.entityActions
			.all((x) => x.entityTypeId === entityType.id)
			.forEach((entityAction) => {
				db.entityActionPermissions.save({
					id: id(),
					entityActionId: entityAction.id,
					roleId: applicantRole.id,
					perform: false,
				})
			})

		db.entityConnections
			.all((x) => x.entityTypeId === entityType.id)
			.forEach((entityConnection) => {
				db.entityConnectionPermissions.save({
					id: id(),
					entityConnectionId: entityConnection.id,
					roleId: applicantRole.id,
					view: true,
					modify: false,
					remove: false,
				})
			})
	})
}

if (!localStorage.getItem('data')) {
	seed()
}
