import React, { FC } from 'react'
import openSlideout, { SlideoutHeader, SlideoutContent } from '@primer/hooks/openSlideout'
import Form, { useForm } from '@primer/components/Form2/Form'
import { watch } from '@primer/components/Form2/utils'
import FormInput from '@primer/components/Form2/FormInput'
import FormSelect from '@primer/components/Form2/FormSelect'
import Stack from '@primer/components/Stack'
import { EntityFieldModel, EntityFieldPermissionModel, RoleModel } from 'types/models'
import FormDate from '@primer/components/Form2/FormDate'
import { CheckIcon, PlusIcon, XIcon } from '@heroicons/react/outline'
import Actions from '@primer/components/Actions'
import FormInputRaw from '@primer/components/Form2/FormInputRaw'
import useFieldArray from '@primer/components/Form2/useFieldArray'
import clsx from 'clsx'
import { SlideoutResolveFn } from '@primer/types'
import { slugify } from '@primer/utils/funcs'
import Table, { TableSchema } from '@primer/components/Table'
import FormLabel from '@primer/components/Form2/FormLabel'
import TableFrame from '@primer/components/TableFrame'
import db from 'db'
import FormCheckbox from '@primer/components/Form2/FormCheckbox'

export const typeOptions = [
	{ value: 'string', label: 'Text' },
	{ value: 'number', label: 'Number' },
	{ value: 'boolean', label: 'Boolean' },
	{ value: 'date', label: 'Date' },
	{ value: 'option', label: 'Options' },
]

type FormEntityFieldModel = EntityFieldModel & { permissions: EntityFieldPermissionModel[] }

interface OpenEntityFieldProps {
	mode?: 'create' | 'edit'
	entityField: FormEntityFieldModel
}

const openEntityField = (props: OpenEntityFieldProps) => {
	return openSlideout<FormEntityFieldModel>({
		render: (close) => <Component close={close} {...props} />,
	})
}

type FormSchema = FormEntityFieldModel

const Component: FC<OpenEntityFieldProps & { close: SlideoutResolveFn<FormEntityFieldModel> }> = ({
	mode = 'create',
	entityField,
	close,
}) => {
	const formContext = useForm<FormSchema>({
		initialValues: entityField as FormSchema,
		onSubmit: (formData) => {
			close({
				...formData,
				key: slugify(formData.name),
			})
		},
	})

	const type = watch(formContext, 'type')
	const permissions = watch(formContext, 'permissions')
	const { values: options, arrayHelpers: optionsHelpers } = useFieldArray('options', formContext)

	const permissionsSchema: TableSchema<RoleModel> = {
		cols: [
			{
				title: 'Role',
				value: (x) => x.name,
			},
			{
				title: 'View',
				value: (x) => {
					const index = permissions.findIndex((y) => y.roleId === x.id)
					return (
						<div className="w-full flex justify-center">
							<FormCheckbox name={`permissions[${index}].view`} />
						</div>
					)
				},
				width: 'minmax(auto, max-content)',
			},
			{
				title: 'Modify',
				value: (x) => {
					const index = permissions.findIndex((y) => y.roleId === x.id)
					return (
						<div className="w-full flex justify-center">
							<FormCheckbox name={`permissions[${index}].modify`} />
						</div>
					)
				},
				width: 'minmax(auto, max-content)',
			},
		],
	}

	return (
		<Form context={formContext} className="contents">
			<SlideoutHeader title={mode === 'create' ? 'Create Entity Field' : 'Edit Entity Field'} />

			<SlideoutContent>
				<Stack className="py-6">
					<div className="px-6">
						<Stack>
							<FormInput
								name="name"
								label="Name"
								className="flex-1"
								validations={{ required: 'Name is required' }}
							/>

							<FormSelect
								name="type"
								label="Type"
								options={typeOptions}
								validations={{ required: 'Type is required' }}
							/>

							{type === 'string' && <FormInput name="defaultValue" label="Default Value" />}
							{type === 'number' && <FormInput name="defaultValue" label="Default Value" type="number" />}
							{type === 'boolean' && (
								<FormSelect
									name="defaultValue"
									label="Default Value"
									options={[
										{ value: 0, label: 'False' },
										{ value: 1, label: 'True' },
									]}
								/>
							)}
							{type === 'date' && <FormDate name="defaultValue" label="Default Value" />}
							{type === 'option' && (
								<div>
									{options.map((x, i) => (
										<div key={i} className="relative">
											<div className="absolute z-[2] top-0 left-0 ml-3 h-full flex items-center justify-center">
												<div className="text-xs font-medium rounded-full w-5 h-5 bg-gray-200 dark:bg-gray-700 dark:text-gray-100 flex items-center justify-center">
													{i + 1}
												</div>
											</div>
											<FormInputRaw
												name={`options[${i}]`}
												inputProps={{
													className: clsx(
														'py-2 pl-12 pr-8 -mb-px relative text-sm w-full border border-gray-300 dark:border-gray-600 focus:z-[1] focus:ring-1 focus:ring-primary-500 focus:border-primary-500 dark:focus:border-primary-500 bg-white dark:bg-gray-800 dark:text-gray-100',
														i === 0 && 'rounded-t-md',
														i === options.length - 1 && 'rounded-b-md'
													),
												}}
											/>
											{options.length > 1 && (
												<div className="absolute z-[1] top-0 right-0 mr-3 h-full flex items-center justify-center">
													<button
														className="rounded-full w-5 h-5 bg-red-100 dark:bg-red-500 dark:bg-opacity-25 text-red-500 flex items-center justify-center focus:ring-2 focus:ring-red-500 transition-shadow"
														type="button"
														onClick={() => optionsHelpers.remove(i)}
													>
														<XIcon className="w-4 h-4" />
													</button>
												</div>
											)}
										</div>
									))}
									<div className="mt-2 flex justify-end">
										<Actions
											actions={{
												title: 'Add option',
												icon: <PlusIcon className="w-4 h-4" />,
												size: 'sm',
												onClick: () => optionsHelpers.push(''),
											}}
										/>
									</div>

									<FormInput name="defaultValue" label="Default Value" />
								</div>
							)}
							<FormInput name="description" label="Description" />

							<div>
								<FormLabel>Permissions</FormLabel>

								<TableFrame className="mt-2">
									<Table schema={permissionsSchema} items={db.roles.all()} className="-my-px" />
								</TableFrame>
							</div>
						</Stack>
					</div>
				</Stack>
			</SlideoutContent>

			<div className="p-6 border-t border-gray-200 dark:border-gray-600 bg-gray-100 dark:bg-gray-750 flex justify-end">
				<Actions
					actions={[
						{
							title: 'Cancel',
							onClick: () => close(),
						},
						{
							title: 'Save',
							icon: <CheckIcon className="w-5 h-5" />,
							intent: 'save',
							type: 'submit',
						},
					]}
				/>
			</div>
		</Form>
	)
}

export default openEntityField
