import { FormikContextType, getIn } from 'formik'

export interface FormValidations {
	required?: string
	min?: {
		value: number
		message: string
	}
	max?: {
		value: number
		message: string
	}
}

export const validate =
	(validations?: FormValidations) =>
	(value: any): string | undefined => {
		if (validations?.required && !value) {
			return validations.required
		}

		if (validations?.min && +value < +validations.min.value) {
			return validations.min.message
		}

		if (validations?.max && +value > +validations.max.value) {
			return validations.max.message
		}

		return undefined
	}

export function watch<T, K extends keyof T>(context: FormikContextType<T>, name: K) {
	return context.values[name]
}

export function watchPath<T>(context: FormikContextType<T>, path: string) {
	return getIn(context.values, path) as unknown
}
