import React, { FC } from 'react'
import { RouteComponentProps } from '@reach/router'
import PageHeading from '@primer/components/PageHeading'
import PageContent from '@primer/components/PageContent'
import Card from '@primer/components/Card'
import Form, { useForm } from '@primer/components/Form2/Form'
import FormInput from '@primer/components/Form2/FormInput'
import FormCheckbox from '@primer/components/Form2/FormCheckbox'
import FormSelect from '@primer/components/Form2/FormSelect'
import FormDate from '@primer/components/Form2/FormDate'
import Actions from '@primer/components/Actions'
import Stack from '@primer/components/Stack'
import useFieldArray from '@primer/components/Form2/useFieldArray'
import Well from '@primer/components/Well'

const fruitOptions = [
	{ label: 'Apple', value: 'apple' },
	{ label: 'Banana', value: 'banana' },
	{ label: 'Orange', value: 'orange' },
]

interface FormData {
	name: string
	newsletter: boolean
	fruit: string | null
	date: Date | null
	options: string[]
	dates: Date[]
}

const Test: FC<RouteComponentProps> = () => {
	const formContext = useForm<FormData>({
		initialValues: {
			name: 'ayy',
			newsletter: true,
			fruit: null,
			date: null,
			options: [],
			dates: [],
		},
		onSubmit: (formData) => {
			console.log('formData', formData)
		},
	})

	const { values, arrayHelpers } = useFieldArray('dates', formContext)

	// do whatever with formContext here
	console.log('name', formContext.values.name)

	return (
		<>
			<PageHeading title="New Form Components" />

			<PageContent>
				<Form context={formContext}>
					<Card>
						<Stack>
							<FormInput name="name" label="Name" validations={{ required: 'Name is required' }} />

							<FormSelect
								name="fruit"
								label="Fruit"
								options={fruitOptions}
								validations={{ required: 'Fruit is required' }}
							/>

							<FormCheckbox name="newsletter" label="Newsletter" />

							<FormDate name="date" label="Date" validations={{ required: 'Date is required' }} />

							<Well>
								<Stack>
									{values.map((_, i) => (
										<div key={i}>
											<FormInput name={`options.${i}`} label={`Option ${i}`} />
										</div>
									))}
									<Actions
										actions={[
											{
												title: 'add',
												onClick: () => arrayHelpers.push(''),
											},
										]}
									/>
								</Stack>
							</Well>

							<Actions
								actions={[
									{
										title: 'Submit',
										intent: 'primary',
										type: 'submit',
									},
								]}
							/>
						</Stack>
					</Card>
				</Form>
			</PageContent>
		</>
	)
}

export default Test
