import clsx, { ClassValue } from 'clsx'
import React, { FC } from 'react'

interface WellProps {
	className?: ClassValue
	onClick?: () => void
}

const Well: FC<WellProps> = ({ children, className, onClick }) => {
	return (
		<div
			className={clsx(
				'p-4 bg-gray-100 dark:bg-gray-750 border border-gray-200 dark:border-gray-600 rounded',
				className
			)}
			onClick={onClick}
		>
			{children}
		</div>
	)
}

export default Well
