import React, { FC } from 'react'
import { navigate, RouteComponentProps } from '@reach/router'
import { ArrowLeftIcon, CheckIcon } from '@heroicons/react/outline'
import Form, { useForm } from '@primer/components/Form2/Form'
import FormInput from '@primer/components/Form2/FormInput'
import Actions from '@primer/components/Actions'
import PageContent from '@primer/components/PageContent'
import PageHeading from '@primer/components/PageHeading'
import Card from '@primer/components/Card'
import Stack from '@primer/components/Stack'
import db from 'db'
import FormSection from '@primer/components/Form2/FormSection'
import { SiteSettings } from 'types/models'
import LayoutSidebar from 'assets/img/layout-sidebar.svg'
import LayoutTopbar from 'assets/img/layout-topbar.svg'
import { watch } from '@primer/components/Form2/utils'
import clsx from 'clsx'
import FormLabel from '@primer/components/Form2/FormLabel'

type FormSchema = SiteSettings

const SiteSettingsForm: FC<RouteComponentProps> = () => {
	const formContext = useForm<FormSchema>({
		initialValues: {
			title: db.siteSettings.get('title') || '',
			layout: db.siteSettings.get('layout') || 'sidebar',
		},
		onSubmit: (formValues) => {
			db.siteSettings.set('title', formValues.title)
			db.siteSettings.set('layout', formValues.layout)

			navigate(`${process.env.PUBLIC_URL}/settings/site-settings`)
		},
	})

	const layout = watch(formContext, 'layout')

	const title = 'Edit Site Settings'

	return (
		<>
			<PageHeading
				pageTitle={title}
				title={
					<div className="flex items-center space-x-3">
						<Actions
							actions={{
								icon: <ArrowLeftIcon className="w-5 h-5 -mx-2" />,
								intent: 'borderless',
								rounded: 'full',
								to: '/settings/site-settings',
							}}
						/>
						<div>{title}</div>
					</div>
				}
			/>

			<PageContent>
				<Form context={formContext}>
					<Stack>
						<FormSection title="Details" subtitle="Basic details about the site">
							<Card>
								<Stack>
									<FormInput
										name="title"
										label="Title"
										validations={{ required: 'Title is required' }}
									/>

									<div>
										<FormLabel>Layout</FormLabel>
										<div className="mt-2 flex space-x-4">
											<button
												className={clsx(
													'w-[200px] h-[200px] relative rounded-md hover:opacity-80 cursor-pointer',
													layout === 'sidebar' && 'ring-2 ring-green-500'
												)}
												onClick={() => {
													formContext.setFieldValue('layout', 'sidebar')
												}}
												type="button"
											>
												<img src={LayoutSidebar} alt="Sidebar" />
											</button>
											<button
												className={clsx(
													'w-[200px] h-[200px] relative rounded-md hover:opacity-80 cursor-pointer',
													layout === 'topbar' && 'ring-2 ring-green-500'
												)}
												onClick={() => {
													formContext.setFieldValue('layout', 'topbar')
												}}
												type="button"
											>
												<img src={LayoutTopbar} alt="Topbar" />
											</button>
										</div>
									</div>
								</Stack>
							</Card>
						</FormSection>

						<div className="flex items-center justify-end">
							<Actions
								actions={[
									{
										title: 'Update',
										intent: 'primary',
										icon: <CheckIcon className="w-5 h-5" />,
										type: 'submit',
									},
								]}
							/>
						</div>
					</Stack>
				</Form>
			</PageContent>
		</>
	)
}

export default SiteSettingsForm
