import React, { FC, Suspense, useMemo, useRef } from 'react'
import clsx from 'clsx'
import { RouteComponentProps, Link } from '@reach/router'
import logo from 'assets/img/workflow-logo-white-text.svg'
import smallLogo from 'assets/img/workflow-logo.svg'
import Loader from '@primer/components/Loader'
import Sidebar from '@primer/components/Sidebar'
import MobileMenu from '@primer/components/MobileMenu'
import { CogIcon, QuestionMarkCircleIcon, ViewGridIcon } from '@heroicons/react/outline'
import Topbar from '@primer/components/Topbar'
import OnRouteChange from '@primer/components/OnRouteChange'
import versions from '@primer/utils/versions'
import semver from 'semver'
import { site } from 'config'
import db from 'db'

const PageSidebar: FC<RouteComponentProps> = ({ children }) => {
	const releases = versions.sort((a, b) => (semver.gt(a.number, b.number) ? -1 : 1))

	const ref = useRef<HTMLDivElement>(null)

	const entityTypes = db.entityTypes.all()

	const links = useMemo(
		() => [
			{
				title: 'Home',
				to: '/home',
				icon: <ViewGridIcon className="w-5 h-5" />,
				exact: true,
			},
			...entityTypes
				.filter((x) => x.mainNav)
				.map((entityType) => ({
					title: entityType.plural || entityType.name,
					to: `/${entityType.key}`,
					icon: <QuestionMarkCircleIcon className="w-5 h-5" />,
					exact: true,
				})),
			{
				title: 'Settings',
				to: '/settings',
				icon: <CogIcon className="w-5 h-5" />,
				exact: false,
			},
		],
		[entityTypes]
	)

	return (
		<div className="h-screen flex flex-col md:flex-row md:overflow-hidden bg-gray-200 dark:bg-gray-800">
			<MobileMenu links={links} />
			<Sidebar
				name="main"
				intent="primary"
				links={links}
				className="hidden md:flex"
				header={(min) => (
					<div className={clsx('flex items-center', min ? 'justify-center' : 'pl-5')}>
						<Link to={`${process.env.PUBLIC_URL}/dashboard`}>
							<img
								src={min ? smallLogo : logo}
								alt="ICRA"
								className={clsx('my-5 h-12', min ? 'ml-2' : '')}
							/>
						</Link>
					</div>
				)}
				footer={(min) => (
					<div className={clsx('flex justify-between items-center', min ? 'flex-col space-y-2' : 'p-4')}>
						<div
							className={clsx(
								'text-gray-400 text-sm font-medium cursor-default',
								min ? 'text-center' : ''
							)}
						>
							{!min ? `Version: ${releases[0].number}` : `v${releases[0].number}`}
						</div>

						{site === 'livetest' ? (
							<div
								className={clsx(
									'py-0.5 -my-0.5 font-medium text-sm leading-6 text-red-300 bg-red-400 dark:bg-red-500 bg-opacity-25 dark:bg-opacity-25',
									min ? 'w-full text-center' : 'px-2.5 rounded-full'
								)}
							>
								LiveTest
							</div>
						) : site === 'demo' ? (
							<div
								className={clsx(
									'py-0.5 -my-0.5 font-medium text-sm leading-6 text-green-300 bg-green-400 dark:bg-green-500 bg-opacity-25 dark:bg-opacity-25',
									min ? 'w-full text-center' : 'px-2.5 rounded-full'
								)}
							>
								Demo
							</div>
						) : site === 'staging' ? (
							<div
								className={clsx(
									'py-0.5 -my-0.5 font-medium text-sm leading-6 text-green-300 bg-green-400 dark:bg-green-500 bg-opacity-25 dark:bg-opacity-25',
									min ? 'w-full text-center' : 'px-2.5 rounded-full'
								)}
							>
								Staging
							</div>
						) : site === 'dev' ? (
							<div
								className={clsx(
									'py-0.5 -my-0.5 font-medium text-sm leading-6 text-indigo-300 bg-indigo-400 dark:bg-indigo-500 bg-opacity-25 dark:bg-opacity-25',
									min ? 'w-full text-center' : 'px-2.5 rounded-full'
								)}
							>
								Dev
							</div>
						) : null}
					</div>
				)}
			/>

			<div className="flex flex-col flex-1 md:w-0 md:overflow-hidden">
				<main className="flex-1 relative z-0 md:overflow-y-auto focus:outline-none" ref={ref}>
					<div className="max-w-7xl mx-auto">
						<div className="border-b border-gray-300 dark:border-gray-700">
							<Topbar />
						</div>
					</div>

					<Suspense
						fallback={
							<div className="flex justify-center items-center h-screen">
								<Loader />
							</div>
						}
					>
						<div className="relative z-10">{children}</div>
					</Suspense>
				</main>
			</div>

			<OnRouteChange
				action={() => {
					if (ref?.current?.scrollTo) ref.current.scrollTo(0, 0)
				}}
			/>
		</div>
	)
}

export default PageSidebar
