import React, { FC, ReactNode, useState } from 'react'
import Select from 'react-select'
import { useField, useFormikContext } from 'formik'
import clsx, { ClassValue } from 'clsx'
import { XIcon } from '@heroicons/react/outline'
import { v4 as uuidv4 } from 'uuid'
import { FormValidations, validate } from './utils'

export interface Option {
	label: string
	value: any
}

interface FormSelectProps {
	name: string
	label?: ReactNode
	options: Option[]
	clearable?: boolean
	disabled?: boolean
	description?: string
	placeholder?: string
	validations?: FormValidations
	hideOptional?: boolean
	className?: ClassValue
	autoFocus?: boolean
	onChange?: (value: any) => void
}

const FormSelect: FC<FormSelectProps> = ({
	name,
	label,
	options,
	clearable,
	disabled = false,
	description,
	placeholder,
	validations = {},
	hideOptional = false,
	className,
	autoFocus,
	onChange: onChangeProp,
}) => {
	const isRequired = validations?.required

	const [{ value, onBlur }, { error, touched }, { setValue, setTouched }] = useField({
		name,
		validate: validate(validations),
	})

	const formik = useFormikContext()

	const [token] = useState(uuidv4())

	return (
		<div className={clsx(className)}>
			<label className="block text-sm font-medium leading-5 text-gray-700 dark:text-gray-200">
				{label && (
					<>
						{label}{' '}
						{!isRequired && !hideOptional && (
							<span className="text-gray-400 dark:text-gray-300 font-normal text-xs leading-4 opacity-75">
								(optional)
							</span>
						)}
					</>
				)}

				<div className="text-[0px] leading-none select-none">{token}</div>

				<Select
					options={options}
					value={
						options.find((x) => {
							// console.log('x.value', x.value, name)
							return (
								x.value === value ||
								(value !== null && x.value === +value) ||
								(x.value?.id !== undefined && x.value?.id === value?.id)
							)
						}) || null
					}
					onChange={(option: any) => {
						setTouched(true)
						setValue(option ? option.value : null)
						if (onChangeProp) onChangeProp(option ? option.value : null)
					}}
					onBlur={(e) => {
						setTouched(true)
						onBlur(e)
					}}
					inputProps={{ autoComplete: 'none', autoCorrect: 'off', spellCheck: 'off' }}
					className={clsx(
						'react-select-container font-normal',
						label && 'mt-1',
						error && touched && 'react-select-container-error'
					)}
					classNamePrefix="react-select"
					placeholder={placeholder}
					isClearable={clearable}
					isDisabled={disabled || formik.isSubmitting}
					autoFocus={autoFocus}
					menuPlacement="auto"
					components={{
						ClearIndicator: ({ hasValue, innerProps: { ref, ...restInnerProps } }) => {
							return hasValue ? (
								<span ref={ref} {...restInnerProps}>
									<XIcon className="mr-8 w-5 h-5 cursor-pointer text-gray-400 hover:text-red-500" />
								</span>
							) : null
						},
						DropdownIndicator: ({ innerProps: { ref } }) => null,
					}}
				/>
			</label>

			{description && !error && !touched && <p className="mt-2 text-sm text-gray-500">{description}</p>}
			{error && touched && <p className="mt-2 text-sm text-red-600">{error}</p>}
		</div>
	)
}

export default FormSelect
