import React, { FC } from 'react'
import { RouteComponentProps } from '@reach/router'
import PageHeading from '@primer/components/PageHeading'
import PageContent from '@primer/components/PageContent'
import Card from '@primer/components/Card'
import db from 'db'
import Actions from '@primer/components/Actions'
import { ArrowLeftIcon } from '@heroicons/react/outline'
import DataListColumns from '@primer/components/DataListColumns'
import { getEntityFieldValue, sortValuesByLatest } from 'utils/funcs'

const ViewEntity: FC<RouteComponentProps<{ entityTypeId: string; id: string }>> = ({ entityTypeId, id }) => {
	const entityType = db.entityTypes.find(entityTypeId)
	const fields = db.entityFields.all((x) => x.entityTypeId === entityTypeId)
	const values = fields.map((x) => db.entityValues.all((y) => y.entityFieldId === x.id).sort(sortValuesByLatest)[0])

	const title = `View ${entityType?.name}`

	return (
		<>
			<PageHeading
				pageTitle={title}
				title={
					<div className="flex items-center space-x-3">
						<Actions
							actions={{
								icon: <ArrowLeftIcon className="w-5 h-5 -mx-2" />,
								intent: 'borderless',
								rounded: 'full',
								to: `/${entityType?.key}`,
							}}
						/>
						<div>{title}</div>
					</div>
				}
			/>

			<PageContent>
				<Card>
					<DataListColumns
						items={fields.map((x, i) => ({
							title: x.name,
							value: getEntityFieldValue(values[i]),
						}))}
					/>
				</Card>
			</PageContent>
		</>
	)
}

export default ViewEntity
