import React, { FC } from 'react'
import { navigate, RouteComponentProps } from '@reach/router'
import { ArrowLeftIcon, CheckIcon } from '@heroicons/react/outline'
import Form, { useForm } from '@primer/components/Form2/Form'
import FormInput from '@primer/components/Form2/FormInput'
import Actions from '@primer/components/Actions'
import PageContent from '@primer/components/PageContent'
import PageHeading from '@primer/components/PageHeading'
import Card, { CardWrapper } from '@primer/components/Card'
import Stack from '@primer/components/Stack'
import FormCheckbox from '@primer/components/Form2/FormCheckbox'
import CardHeader from '@primer/components/CardHeader'
import db from 'db'
import FormSection from '@primer/components/Form2/FormSection'
import Table, { TableSchema } from '@primer/components/Table'
import TableFrame from '@primer/components/TableFrame'
import { EntityFieldPermissionModel, EntityActionPermissionModel, EntityConnectionPermissionModel } from 'types/models'

interface PermissionSet<T> {
	// [key: string]: {
	// 	id: string
	// 	view: boolean
	// 	modify: boolean
	// 	remove: boolean
	// }
	[key: string]: T
}

interface Permissions {
	[key: string]: {
		fields: PermissionSet<EntityFieldPermissionModel>
		actions: PermissionSet<EntityActionPermissionModel>
		connections: PermissionSet<EntityConnectionPermissionModel>
	}
}

interface FormSchema {
	name: string
	entityTypes: Permissions
}

const RoleForm: FC<RouteComponentProps<{ id: string; mode: 'create' | 'edit' }>> = ({ id, mode }) => {
	const originalRole = db.roles.find(id)

	const formContext = useForm<FormSchema>({
		initialValues: {
			name: originalRole?.name || '',
			entityTypes: db.entityTypes.all().reduce<Permissions>((list, entityType) => {
				return {
					...list,
					[entityType.key]: {
						fields: db.entityFields
							.all((x) => x.entityTypeId === entityType.id)
							.reduce<PermissionSet<EntityFieldPermissionModel>>(
								(items, x) => ({
									...items,
									[x.key]: db.entityFieldPermissions.find(
										(y) => y.entityFieldId === x.id && y.roleId === id
									) || {
										id: '',
										view: true,
										modify: true,
										roleId: id || '',
										entityFieldId: x.id,
									},
								}),
								{}
							),
						actions: db.entityActions
							.all((x) => x.entityTypeId === entityType.id)
							.reduce<PermissionSet<EntityActionPermissionModel>>(
								(items, x) => ({
									...items,
									[x.key]: db.entityActionPermissions.find(
										(y) => y.entityActionId === x.id && y.roleId === id
									) || {
										id: '',
										perform: true,
										roleId: id || '',
										entityActionId: x.id,
									},
								}),
								{}
							),
						connections: db.entityConnections
							.all((x) => x.entityTypeId === entityType.id)
							.reduce<PermissionSet<EntityConnectionPermissionModel>>(
								(items, x) => ({
									...items,
									[x.key]: db.entityConnectionPermissions.find(
										(y) => y.entityConnectionId === x.id && y.roleId === id
									) || {
										id: '',
										view: true,
										modify: true,
										remove: true,
										roleId: id || '',
										entityConnectionId: x.id,
									},
								}),
								{}
							),
					},
				}
			}, {}),
		},
		onSubmit: (formValues) => {
			db.roles.save({
				...(originalRole || {}),
				name: formValues.name,
			})

			db.entityTypes.all().forEach((entityType) => {
				Object.values(formValues.entityTypes[entityType.key].fields).forEach((val) => {
					db.entityFieldPermissions.save(val)
				})

				Object.values(formValues.entityTypes[entityType.key].actions).forEach((val) => {
					db.entityActionPermissions.save(val)
				})

				Object.values(formValues.entityTypes[entityType.key].connections).forEach((val) => {
					db.entityConnectionPermissions.save(val)
				})
			})

			navigate(`${process.env.PUBLIC_URL}/settings/roles`)
		},
	})

	const fieldSchema = (entityTypeKey: string): TableSchema<any> => ({
		cols: [
			{
				title: 'Field',
				value: (x) => x.name,
			},
			{
				title: 'View',
				value: (x, i) => (
					<div className="w-full flex justify-center">
						<FormCheckbox name={`entityTypes.${entityTypeKey}.fields.${x.key}.view`} />
					</div>
				),
				width: 'minmax(auto, max-content)',
			},
			{
				title: 'Modify',
				value: (x, i) => (
					<div className="w-full flex justify-center">
						<FormCheckbox name={`entityTypes.${entityTypeKey}.fields.${x.key}.modify`} />
					</div>
				),
				width: 'minmax(auto, max-content)',
			},
		],
	})

	const actionSchema = (entityTypeKey: string): TableSchema<any> => ({
		cols: [
			{
				title: 'Action',
				value: (x) => x.name,
			},
			{
				title: 'Perform',
				value: (x, i) => (
					<div className="w-full flex justify-center">
						<FormCheckbox name={`entityTypes.${entityTypeKey}.actions.${x.key}.perform`} />
					</div>
				),
				width: 'minmax(auto, max-content)',
			},
		],
	})

	const connectionSchema = (entityTypeKey: string): TableSchema<any> => ({
		cols: [
			{
				title: 'Connection',
				value: (x) => x.name,
			},
			{
				title: 'View',
				value: (x, i) => (
					<div className="w-full flex justify-center">
						<FormCheckbox name={`entityTypes.${entityTypeKey}.connections.${x.key}.view`} />
					</div>
				),
				width: 'minmax(auto, max-content)',
			},
			{
				title: 'Modify',
				value: (x, i) => (
					<div className="w-full flex justify-center">
						<FormCheckbox name={`entityTypes.${entityTypeKey}.connections.${x.key}.modify`} />
					</div>
				),
				width: 'minmax(auto, max-content)',
			},
			{
				title: 'Remove',
				value: (x, i) => (
					<div className="w-full flex justify-center">
						<FormCheckbox name={`entityTypes.${entityTypeKey}.connections.${x.key}.remove`} />
					</div>
				),
				width: 'minmax(auto, max-content)',
			},
		],
	})

	const title = mode === 'create' ? 'Create Role' : 'Edit Role'

	return (
		<>
			<PageHeading
				pageTitle={title}
				title={
					<div className="flex items-center space-x-3">
						<Actions
							actions={{
								icon: <ArrowLeftIcon className="w-5 h-5 -mx-2" />,
								intent: 'borderless',
								rounded: 'full',
								to: '/settings/roles',
							}}
						/>
						<div>{title}</div>
					</div>
				}
			/>

			<PageContent>
				<Form context={formContext}>
					<Stack>
						<FormSection title="Details" subtitle="Basic details about the role">
							<Card>
								<Stack>
									<FormInput
										name="name"
										label="Name"
										validations={{ required: 'Name is required' }}
									/>
								</Stack>
							</Card>
						</FormSection>

						<FormSection
							title="Permissions"
							subtitle="A view of permissions for all existing entity types and their fields/actions/connections"
						>
							<Stack>
								{db.entityTypes.all().map((entityType) => (
									<CardWrapper key={entityType.key}>
										<CardHeader title={entityType.name} />
										<div className="rounded-b-md overflow-hidden">
											<Stack className="p-6">
												{(() => {
													let items = db.entityFields.all(
														(x) => x.entityTypeId === entityType.id
													)

													return (
														items.length > 0 && (
															<TableFrame>
																<Table
																	schema={fieldSchema(entityType.key)}
																	items={items}
																	className="-my-px"
																/>
															</TableFrame>
														)
													)
												})()}

												{(() => {
													let items = db.entityActions.all(
														(x) => x.entityTypeId === entityType.id
													)

													return (
														items.length > 0 && (
															<TableFrame>
																<Table
																	schema={actionSchema(entityType.key)}
																	items={items}
																	className="-my-px"
																/>
															</TableFrame>
														)
													)
												})()}

												{(() => {
													let items = db.entityConnections.all(
														(x) => x.entityTypeId === entityType.id
													)

													return (
														items.length > 0 && (
															<TableFrame>
																<Table
																	schema={connectionSchema(entityType.key)}
																	items={items}
																	className="-my-px"
																/>
															</TableFrame>
														)
													)
												})()}
											</Stack>
										</div>
									</CardWrapper>
								))}
							</Stack>
						</FormSection>

						<div className="flex items-center justify-end">
							<Actions
								actions={[
									{
										title: mode === 'create' ? 'Create' : 'Save',
										intent: 'primary',
										icon: <CheckIcon className="w-5 h-5" />,
										type: 'submit',
									},
								]}
							/>
						</div>
					</Stack>
				</Form>
			</PageContent>
		</>
	)
}

export default RoleForm
