import React, { FC, Suspense } from 'react'
import { Router, Redirect, RouteComponentProps } from '@reach/router'
import db from 'db'

import PageSidebar from '@primer/components/Layouts/PageSidebar'
import PageTopbar from '@primer/components/Layouts/PageTopbar'

// logged in
import Home from 'pages/Home'
import NotFound from 'pages/NotFound'

// entity
import Entities from 'pages/Entities/Entities'
import ViewEntity from 'pages/Entities/ViewEntity'
import EntityForm from 'pages/Entities/EntityForm'

// settings
import Settings from 'pages/Settings/Settings'

// notifications
import Notifications from 'pages/Settings/Notifications/Notifications'
import NotificationForm from 'pages/Settings/Notifications/NotificationForm'

// templates
import Templates from 'pages/Settings/Templates/Templates'
import TemplateForm from 'pages/Settings/Templates/TemplateForm'

// roles
import Roles from 'pages/Settings/Roles/Roles'
import RoleForm from 'pages/Settings/Roles/RoleForm'

// entities
import EntityTypes from 'pages/Settings/EntityTypes/EntityTypes'
import EntityTypeForm from 'pages/Settings/EntityTypes/EntityTypeForm'

// Modules
import Modules from 'pages/Settings/Modules/Modules'
import AddModule from 'pages/Settings/Modules/AddModule'

// SiteSettings
import SiteSettings from 'pages/Settings/SiteSettings/SiteSettings'
import SiteSettingsForm from 'pages/Settings/SiteSettings/SiteSettingsForm'

import Test from 'pages/Test'

const isDev = !process.env.NODE_ENV || process.env.NODE_ENV === 'development'

const Route: FC<RouteComponentProps> = ({ children }) => <>{children}</>

console.log('process.env', process.env)

const Router2 = () => {
	const Wrapper = db.siteSettings.get('layout') === 'sidebar' ? PageSidebar : PageTopbar

	const entityTypes = db.entityTypes.all()

	return (
		<Suspense fallback={null}>
			<Router basepath={process.env.PUBLIC_URL}>
				<Wrapper path="/">
					<Redirect from="/" to="home" noThrow={isDev} />
					<NotFound default />
					<Home path="/home" />
					<Home path="/events" />
					<Home path="/users" />

					{entityTypes.map((entityType) => (
						<Route key={entityType.id} path={`/${entityType.key}`}>
							<NotFound default />
							<Entities path="/" entityTypeId={entityType.id} />
							<ViewEntity path=":id" entityTypeId={entityType.id} />
							<EntityForm path="/create" entityTypeId={entityType.id} mode="create" />
							<EntityForm path=":id/edit" entityTypeId={entityType.id} mode="edit" />
						</Route>
					))}

					<Settings path="/settings">
						<NotFound default />
						<Redirect from="/" to="entity-types" noThrow={isDev} />

						<Route path="/entity-types">
							<NotFound default />
							<EntityTypes path="/" />
							<EntityTypeForm path="/create" mode="create" />
							<EntityTypeForm path=":id/edit" mode="edit" />
						</Route>

						<Route path="/roles">
							<NotFound default />
							<Roles path="/" />
							<RoleForm path="/create" mode="create" />
							<RoleForm path=":id/edit" mode="edit" />
						</Route>

						<Route path="/templates">
							<NotFound default />
							<Templates path="/" />
							<TemplateForm path="/create" mode="create" />
							<TemplateForm path=":id/edit" mode="edit" />
						</Route>

						<Route path="/notifications">
							<NotFound default />
							<Notifications path="/" />
							<NotificationForm path="/create" mode="create" />
							<NotificationForm path=":id/edit" mode="edit" />
						</Route>

						<Route path="/modules">
							<NotFound default />
							<Modules path="/" />
							<AddModule path="/add" />
						</Route>

						<Route path="/site-settings">
							<NotFound default />
							<SiteSettings path="/" />
							<SiteSettingsForm path="/edit" />
						</Route>
					</Settings>

					<Test path="/test" />
				</Wrapper>
			</Router>
		</Suspense>
	)
}

export default Router2
