import React, { FC, useState } from 'react'
import { v4 as uuid } from 'uuid'
import Card, { CardWrapper } from '@primer/components/Card'
import PageHeading from '@primer/components/PageHeading'
import PageContent from '@primer/components/PageContent'
import { navigate, RouteComponentProps } from '@reach/router'
import { ArrowLeftIcon, CheckIcon, PencilIcon, PlusIcon, XIcon } from '@heroicons/react/outline'
import Actions from '@primer/components/Actions'
import Stack from '@primer/components/Stack'
import Form, { useForm } from '@primer/components/Form2/Form'
import FormInput from '@primer/components/Form2/FormInput'
import db from 'db'
import {
	EntityActionModel,
	EntityActionPermissionModel,
	EntityConnectionModel,
	EntityConnectionPermissionModel,
	EntityFieldModel,
	EntityFieldPermissionModel,
} from 'types/models'
import useFieldArray from '@primer/components/Form2/useFieldArray'
import FormSection from '@primer/components/Form2/FormSection'
import Table, { TableSchema } from '@primer/components/Table'
import openEntityField, { typeOptions } from 'slideouts/openEntityField'
import openEntityAction from 'slideouts/openEntityAction'
import { confirm } from '@primer/alerts'
import { slugify } from '@primer/utils/funcs'
import openEntityConnection, {
	connectionTypeOptions,
	oppositeConnectionTypeOptions,
} from 'slideouts/openEntityConnection'
import produce from 'immer'
import FormToggle from '@primer/components/Form2/FormToggle'

type FormEntityFieldModel = EntityFieldModel & { permissions: EntityFieldPermissionModel[] }
type FormEntityActionModel = EntityActionModel & { permissions: EntityActionPermissionModel[] }
type FormEntityConnectionModel = EntityConnectionModel & { permissions: EntityConnectionPermissionModel[] }

interface FormSchema {
	name: string
	plural: string
	mainNav: boolean
	entityFields: FormEntityFieldModel[]
	entityActions: FormEntityActionModel[]
	entityConnections: FormEntityConnectionModel[]
}

const EntityTypeForm: FC<RouteComponentProps<{ id: string; mode: 'create' | 'edit' }>> = ({ id, mode }) => {
	const original = db.entityTypes.find(id)

	const originalEntityFields = db.entityFields.all((x) => x.entityTypeId === id)
	const originalEntityActions = db.entityActions.all((x) => x.entityTypeId === id)
	const originalEntityConnections = db.entityConnections.all((x) => x.entityTypeId === id || x.relationId === id)

	const formContext = useForm<FormSchema>({
		initialValues: {
			name: original?.name || '',
			plural: original?.plural || '',
			mainNav: original?.mainNav || false,
			entityFields: originalEntityFields
				? originalEntityFields.map((x) => ({
						...x,
						permissions: db.entityFieldPermissions.all((y) => y.entityFieldId === x.id),
				  }))
				: [],
			entityActions: originalEntityActions
				? originalEntityActions.map((x) => ({
						...x,
						permissions: db.entityActionPermissions.all((y) => y.entityActionId === x.id),
				  }))
				: [],
			entityConnections: originalEntityConnections
				? originalEntityConnections.map((x) => ({
						...x,
						permissions: db.entityConnectionPermissions.all((y) => y.entityConnectionId === x.id),
				  }))
				: [],
		},
		onSubmit: (formSchema) => {
			const newEntityType = db.entityTypes.save({
				...original,
				key: slugify(formSchema.name),
				name: formSchema.name,
				plural: formSchema.plural,
				mainNav: formSchema.mainNav,
			})

			formSchema.entityFields.forEach(({ permissions, ...entityField }) => {
				db.entityFields.save({
					...entityField,
					entityTypeId: newEntityType.id,
				})

				permissions.forEach((x) => {
					db.entityFieldPermissions.save({
						...x,
						...(x.id ? {} : { id: uuid() }), // set id if it's not a thing
					})
				})
			})

			formSchema.entityActions.forEach(({ permissions, ...entityAction }) => {
				db.entityActions.save({
					...entityAction,
					entityTypeId: newEntityType.id,
				})

				permissions.forEach((x) => {
					db.entityActionPermissions.save({
						...x,
						...(x.id ? {} : { id: uuid() }), // set id if it's not a thing
					})
				})
			})

			formSchema.entityConnections.forEach(({ permissions, ...entityConnection }) => {
				db.entityConnections.save({
					...entityConnection,
					entityTypeId: newEntityType.id,
				})

				permissions.forEach((x) => {
					db.entityConnectionPermissions.save({
						...x,
						...(x.id ? {} : { id: uuid() }), // set id if it's not a thing
					})
				})
			})

			toDelete.entityFields.forEach(db.entityFields.remove)
			toDelete.entityActions.forEach(db.entityActions.remove)
			toDelete.entityConnections.forEach(db.entityConnections.remove)

			navigate(`${process.env.PUBLIC_URL}/settings/entity-types`)
		},
	})

	const { values: entityFields, arrayHelpers: entityFieldHelpers } = useFieldArray('entityFields', formContext)
	const { values: entityActions, arrayHelpers: entityActionsHelpers } = useFieldArray('entityActions', formContext)
	const { values: entityConnections, arrayHelpers: entityConnectionsHelpers } = useFieldArray(
		'entityConnections',
		formContext
	)

	const [toDelete, setToDelete] = useState<{
		entityFields: string[]
		entityActions: string[]
		entityConnections: string[]
	}>({
		entityFields: [],
		entityActions: [],
		entityConnections: [],
	})

	const entityFieldSchema: TableSchema<FormEntityFieldModel> = {
		cols: [
			{
				title: 'Name',
				value: (x) => x.name,
			},
			{
				title: 'Type',
				value: (x) => typeOptions.find((y) => y.value === x.type)?.label || '',
			},
		],
		actions: (x, i) => [
			{
				icon: <PencilIcon className="w-5 h-5" />,
				intent: 'menu',
				onClick: async () => {
					const editedEntityField = await openEntityField({ mode: 'edit', entityField: x })
					if (editedEntityField) {
						entityFieldHelpers.replace(i, editedEntityField)
					}
				},
			},
			{
				icon: <XIcon className="w-5 h-5" />,
				intent: 'menu-danger',
				onClick: () => {
					confirm({
						title: 'Are you sure?',
						message: 'This cannot be undone',
						onAccept: () => {
							entityFieldHelpers.remove(i)
							setToDelete(
								produce((draft) => {
									draft.entityFields.push(x.id)
								})
							)
						},
					})
				},
			},
		],
	}

	const entityActionsSchema: TableSchema<FormEntityActionModel> = {
		cols: [
			{
				title: 'Name',
				value: (x) => x.name,
			},
		],
		actions: (x, i) => [
			{
				icon: <PencilIcon className="w-5 h-5" />,
				intent: 'menu',
				onClick: async () => {
					const editedEntityAction = await openEntityAction({ mode: 'edit', entityAction: x, entityFields })
					if (editedEntityAction) {
						entityActionsHelpers.replace(i, editedEntityAction)
					}
				},
			},
			{
				icon: <XIcon className="w-5 h-5" />,
				intent: 'menu-danger',
				onClick: () => {
					confirm({
						title: 'Are you sure?',
						message: 'This cannot be undone',
						onAccept: () => {
							entityActionsHelpers.remove(i)
							setToDelete(
								produce((draft) => {
									draft.entityActions.push(x.id)
								})
							)
						},
					})
				},
			},
		],
	}

	const connectionSchema: TableSchema<FormEntityConnectionModel> = {
		cols: [
			{
				title: 'Name',
				value: (x) => x.name,
			},
			{
				title: 'Related Entity',
				value: (x) =>
					x.entityTypeId === id
						? db.entityTypes.find((y) => y.id === x.relationId)?.name
						: db.entityTypes.find((y) => y.id === x.entityTypeId)?.name,
			},
			{
				title: 'Cardinality',
				value: (x) =>
					x.entityTypeId === id
						? connectionTypeOptions.find((y) => y.value === x.type)?.label
						: oppositeConnectionTypeOptions.find((y) => y.value === x.type)?.label,
			},
		],
		actions: (x, i) => [
			x.entityTypeId === id && {
				icon: <PencilIcon className="w-5 h-5" />,
				intent: 'menu',
				onClick: async () => {
					const editedEntityConnection = await openEntityConnection({ mode: 'edit', entityConnection: x })
					if (editedEntityConnection) {
						entityConnectionsHelpers.replace(i, editedEntityConnection)
					}
				},
			},
			x.entityTypeId === id && {
				icon: <XIcon className="w-5 h-5" />,
				intent: 'menu-danger',
				onClick: () => {
					confirm({
						title: 'Are you sure?',
						message: 'This cannot be undone',
						onAccept: () => {
							entityConnectionsHelpers.remove(i)
							setToDelete(
								produce((draft) => {
									draft.entityConnections.push(x.id)
								})
							)
						},
					})
				},
			},
		],
	}

	const title = mode === 'create' ? 'Create Entity Type' : 'Edit Entity Type'

	return (
		<>
			<PageHeading
				pageTitle={title}
				title={
					<div className="flex items-center space-x-3">
						<Actions
							actions={{
								icon: <ArrowLeftIcon className="w-5 h-5 -mx-2" />,
								intent: 'borderless',
								rounded: 'full',
								to: '/settings/entity-types',
							}}
						/>
						<div>{title}</div>
					</div>
				}
			/>

			<Form context={formContext}>
				<PageContent>
					<Stack dividers>
						<FormSection title="Details" subtitle="The name and different types of labels for the entity">
							<Card>
								<Stack>
									<FormInput
										name="name"
										label="Name"
										validations={{ required: 'Name is required' }}
									/>

									<FormInput name="plural" label="Plural" />

									<FormToggle name="mainNav" label="Show in Main Nav" />
								</Stack>
							</Card>
						</FormSection>

						<FormSection
							title="Field Types"
							subtitle="Field Types are custom fields that are attached to this entity, that can be configured to contain many different kinds of data"
						>
							<Stack>
								<CardWrapper className="rounded-md overflow-hidden">
									<Table items={entityFields} schema={entityFieldSchema} className="-my-px" />
								</CardWrapper>

								<div className="flex justify-end">
									<Actions
										actions={{
											title: 'Add Field Type',
											icon: <PlusIcon className="w-5 h-5" />,
											intent: 'invisible',
											onClick: async () => {
												let id = uuid()
												const newEntityField = await openEntityField({
													entityField: {
														id,
														entityTypeId: '',
														key: '',
														name: '',
														plural: '',
														template: '',
														type: 'string',
														options: [''],
														permissions: db.roles
															.all()
															.map<EntityFieldPermissionModel>((x) => ({
																id: '',
																roleId: x.id,
																entityFieldId: id,
																view: true,
																modify: true,
															})),
													},
												})

												if (newEntityField) entityFieldHelpers.push(newEntityField)
											},
										}}
									/>
								</div>
							</Stack>
						</FormSection>

						<FormSection
							title="Actions"
							subtitle="Actions are custom interactions that the user can use do to this entity. They can modify a field, or be used as triggers for notifications."
						>
							<Stack>
								<CardWrapper className="rounded-md overflow-hidden">
									<Table items={entityActions} schema={entityActionsSchema} className="-my-px" />
								</CardWrapper>

								<div className="flex justify-end">
									<Actions
										actions={{
											title: 'Add Action',
											icon: <PlusIcon className="w-5 h-5" />,
											intent: 'invisible',
											onClick: async () => {
												let id = uuid()
												const newEntityAction = await openEntityAction({
													entityAction: {
														id,
														entityTypeId: '',
														key: '',
														name: '',
														changes: [],
														permissions: db.roles
															.all()
															.map<EntityActionPermissionModel>((x) => ({
																id: '',
																roleId: x.id,
																entityActionId: id,
																perform: true,
															})),
													},
													entityFields,
												})

												if (newEntityAction) entityActionsHelpers.push(newEntityAction)
											},
										}}
									/>
								</div>
							</Stack>
						</FormSection>

						<FormSection
							title="Connections"
							subtitle={
								<div className="space-y-3">
									<p>
										Connections are a way to define a relationship between this entity type, and
										another entity type.
									</p>
									<p>
										For example, if a "case has many events", then a case should have a connection
										named "Events", set to the Event entity and a cardinality of "Many"
									</p>
								</div>
							}
						>
							<Stack>
								<CardWrapper className="rounded-md overflow-hidden">
									<Table items={entityConnections} schema={connectionSchema} className="-my-px" />
								</CardWrapper>

								<div className="flex justify-end">
									<Actions
										actions={{
											title: 'Add Connection',
											icon: <PlusIcon className="w-5 h-5" />,
											intent: 'invisible',
											onClick: async () => {
												let id = uuid()
												const newConnection = await openEntityConnection({
													entityConnection: {
														id,
														key: '',
														name: '',
														entityTypeId: '',
														relationId: '',
														type: 'one-to-many',
														permissions: db.roles
															.all()
															.map<EntityConnectionPermissionModel>((x) => ({
																id: '',
																roleId: x.id,
																entityConnectionId: id,
																view: true,
																modify: true,
																remove: true,
															})),
													},
												})

												if (newConnection) entityConnectionsHelpers.push(newConnection)
											},
										}}
									/>
								</div>
							</Stack>
						</FormSection>

						<div className="flex justify-end">
							<Actions
								actions={[
									{
										title: mode === 'create' ? 'Create' : 'Save',
										intent: 'save',
										icon: <CheckIcon className="w-5 h-5" />,
										type: 'submit',
									},
								]}
							/>
						</div>
					</Stack>
				</PageContent>
			</Form>
		</>
	)
}

export default EntityTypeForm
