import React, { FC } from 'react'
import { v4 as uuid } from 'uuid'
import openSlideout, { SlideoutHeader, SlideoutContent } from '@primer/hooks/openSlideout'
import Form, { useForm } from '@primer/components/Form2/Form'
import FormInput from '@primer/components/Form2/FormInput'
import Stack from '@primer/components/Stack'
import { EntityActionModel, EntityActionPermissionModel, EntityFieldModel, RoleModel } from 'types/models'
import { CheckIcon, PlusIcon } from '@heroicons/react/outline'
import Actions from '@primer/components/Actions'
import useFieldArray from '@primer/components/Form2/useFieldArray'
import FormLabel from '@primer/components/Form2/FormLabel'
import { isNotNone, SlideoutResolveFn } from '@primer/types'
import FormSelect from '@primer/components/Form2/FormSelect'
import Well from '@primer/components/Well'
import { slugify } from '@primer/utils/funcs'
import Table, { TableSchema } from '@primer/components/Table'
import TableFrame from '@primer/components/TableFrame'
import db from 'db'
import FormCheckbox from '@primer/components/Form2/FormCheckbox'
import { watch } from '@primer/components/Form2/utils'

type FormEntityActionModel = EntityActionModel & { permissions: EntityActionPermissionModel[] }

interface OpenEntityActionProps {
	mode?: 'create' | 'edit'
	entityAction: FormEntityActionModel
	entityFields: EntityFieldModel[]
}

const openEntityAction = (props: OpenEntityActionProps) => {
	return openSlideout<FormEntityActionModel>({
		size: '2xl',
		render: (close) => <Component close={close} {...props} />,
	})
}

type FormSchema = FormEntityActionModel

const Component: FC<OpenEntityActionProps & { close: SlideoutResolveFn<FormEntityActionModel> }> = ({
	mode = 'create',
	entityAction,
	entityFields,
	close,
}) => {
	const formContext = useForm<FormSchema>({
		initialValues: entityAction as FormSchema,
		onSubmit: (formData) => {
			close({
				...formData,
				key: slugify(formData.name),
			})
		},
	})

	const permissions = watch(formContext, 'permissions')
	const { values: changes, arrayHelpers: changesHelpers } = useFieldArray('changes', formContext)

	const getEntityField = (id?: string) => entityFields.find((x) => x.id === id)

	const permissionsSchema: TableSchema<RoleModel> = {
		cols: [
			{
				title: 'Role',
				value: (x) => x.name,
			},
			{
				title: 'Perform',
				value: (x) => {
					const index = permissions.findIndex((y) => y.roleId === x.id)
					return (
						<div className="w-full flex justify-center">
							<FormCheckbox name={`permissions[${index}].perform`} />
						</div>
					)
				},
				width: 'minmax(auto, max-content)',
			},
		],
	}

	return (
		<Form context={formContext} className="contents">
			<SlideoutHeader title={mode === 'create' ? 'Create Action' : 'Edit Action'} />

			<SlideoutContent>
				<Stack className="py-6">
					<div className="px-6">
						<Stack>
							<FormInput
								name="name"
								label="Name"
								className="flex-1"
								validations={{ required: 'Name is required' }}
							/>

							<div>
								<FormLabel>Changes</FormLabel>
								<Well className="mt-2">
									<Stack>
										{changes.filter(isNotNone).map((change, i) => {
											let entityField = getEntityField(change.entityFieldId)

											return (
												<div key={i} className="flex items-center space-x-3">
													<FormLabel>Set</FormLabel>
													<div className="w-[200px]">
														<FormSelect
															name={`changes[${i}].entityFieldId`}
															options={entityFields.map((x) => ({
																value: x.id,
																label: x.name,
															}))}
														/>
													</div>

													{entityField?.type === 'boolean' && (
														<>
															<FormLabel>to</FormLabel>
															<div className="w-[200px]">
																<FormSelect
																	name={`changes[${i}].newValue`}
																	options={[
																		{ label: 'True', value: 'true' },
																		{ label: 'False', value: 'false' },
																	]}
																/>
															</div>
														</>
													)}

													{entityField?.type === 'string' && (
														<>
															<FormLabel>to</FormLabel>
															<div className="w-[200px]">
																<FormInput name={`changes[${i}].newValue`} />
															</div>
														</>
													)}

													{entityField?.type === 'date' && (
														<>
															<FormLabel>to</FormLabel>
															<div className="w-[300px]">
																<FormSelect
																	name={`changes[${i}].newValue`}
																	options={[
																		{
																			label: 'Current Time',
																			value: 'current-time',
																		},
																	]}
																	className="flex-1"
																/>
															</div>
														</>
													)}

													{entityField?.type === 'option' && (
														<>
															<FormLabel>to</FormLabel>
															<div className="w-[300px]">
																<FormSelect
																	name={`changes[${i}].newValue`}
																	options={entityField.options.map((x) => ({
																		label: x,
																		value: x,
																	}))}
																	className="flex-1"
																/>
															</div>
														</>
													)}
												</div>
											)
										})}
										<Actions
											actions={{
												title: 'Add change',
												icon: <PlusIcon className="w-5 h-5" />,
												onClick: () =>
													changesHelpers.push({
														id: uuid(),
														userDefined: true,
													}),
											}}
										/>
									</Stack>
								</Well>
							</div>

							<div>
								<FormLabel>Permissions</FormLabel>
								<TableFrame className="mt-2">
									<Table schema={permissionsSchema} items={db.roles.all()} className="-my-px" />
								</TableFrame>
							</div>
						</Stack>
					</div>
				</Stack>
			</SlideoutContent>

			<div className="p-6 border-t border-gray-200 dark:border-gray-600 bg-gray-100 dark:bg-gray-750 flex justify-end">
				<Actions
					actions={[
						{
							title: 'Cancel',
							onClick: () => close(),
						},
						{
							title: 'Save',
							icon: <CheckIcon className="w-5 h-5" />,
							intent: 'save',
							type: 'submit',
						},
					]}
				/>
			</div>
		</Form>
	)
}

export default openEntityAction
