export type Unsaved<T> = Omit<T, 'id'>

export const isUnsaved = <T>(x: any): x is Unsaved<T> => typeof x === 'object' && typeof x.id === 'undefined'
export const isSaved = <T>(x: any): x is Unsaved<T> => typeof x === 'object' && typeof x.id === 'string'

export interface SiteSettings {
	title: string
	layout: 'sidebar' | 'topbar'
}

export interface Model {
	id: string
	created?: string
}

export interface EntityTypeModel extends Model {
	key: string
	name: string
	plural?: string
	mainNav: boolean
}

export interface EntityModel extends Model {
	entityTypeId: string
}

export type EntityField = 'string' | 'number' | 'date' | 'boolean' | 'option'

export interface EntityFieldModel extends Model {
	entityTypeId: string
	key: string
	name: string
	plural?: string
	template?: string
	type: EntityField
	options: string[]
	defaultValue?: string
}

export interface EntityValueModel extends Model {
	entityFieldId: string
	entityId: string
	stringValue?: string
	dateValue?: Date
	booleanValue?: boolean
	numberValue?: number
}

export interface EntityActionModel extends Model {
	entityTypeId: string
	key: string
	name: string
	changes?: EntityActionChangeModel[]
}

export interface EntityActionChangeModel extends Model {
	entityActionId: string
	entityFieldId: string
	userDefined: boolean
}

export type ConnectionType = 'one-to-one' | 'one-to-many' | 'many-to-many'

export interface EntityConnectionModel extends Model {
	key: string
	name: string
	entityTypeId: string
	relationId: string
	type: ConnectionType
}

export interface NotificationModel extends Model {
	name: string
	focus: string
	enabled: boolean
	enabledDate?: Date
	triggerType: string
	triggerRepeat: number
	triggerEvery: string
	triggerYearIn: string
	triggerMonthOn: string
	triggerWeekOn: string
	triggerDayAt: string
	triggerAction: string
	obeyTriggerTiming: boolean
	triggerTimingType: string
	triggerTiming: string
	triggerDate: string
	subject: string
	content: string
	ruleGroups: NotificationRuleGroup[]
}

export interface NotificationRuleGroup {
	rules: NotificationRule[]
}

export interface NotificationRule {
	entity: string
	field: string
	operator: string
	value: string
}

export interface TemplateModel {
	id: string
	name: string
	content: string
}

export interface RoleModel extends Model {
	name: string
}

export interface EntityTypePermissionModel extends Model {
	roleId: string
	entityTypeId: string
	view: boolean
	modify: boolean
	remove: boolean
}

export interface EntityFieldPermissionModel extends Model {
	roleId: string
	entityFieldId: string
	view: boolean
	modify: boolean
}

export interface EntityActionPermissionModel extends Model {
	roleId: string
	entityActionId: string
	perform: boolean
}

export interface EntityConnectionPermissionModel extends Model {
	roleId: string
	entityConnectionId: string
	view: boolean
	modify: boolean
	remove: boolean
}
