import React, { FC, useState } from 'react'
import Form, { useForm } from '@primer/components/Form2/Form'
import FormInput from '@primer/components/Form2/FormInput'
import FormSelect from '@primer/components/Form2/FormSelect'
import { navigate, RouteComponentProps } from '@reach/router'
import PageHeading from '@primer/components/PageHeading'
import PageContent from '@primer/components/PageContent'
import Card from '@primer/components/Card'
import db from 'db'
import Actions from '@primer/components/Actions'
import { ArrowLeftIcon, CheckIcon } from '@heroicons/react/outline'
import Stack from '@primer/components/Stack'
import FormDate from '@primer/components/Form2/FormDate'
import FormToggle from '@primer/components/Form2/FormToggle'
import { sortValuesByLatest } from 'utils/funcs'

interface FormSchema {
	fields: {
		[x: string]: {
			id?: string
			stringValue?: string
			dateValue?: Date
			booleanValue?: boolean
			numberValue?: number
		}
	}
}

const EntityForm: FC<RouteComponentProps<{ entityTypeId: string; mode: 'create' | 'edit'; id: string }>> = ({
	entityTypeId,
	mode,
	id,
}) => {
	const entityType = db.entityTypes.find(entityTypeId)
	const fields = db.entityFields.all((x) => x.entityTypeId === entityTypeId)

	const [initialValues] = useState<FormSchema>({
		fields: fields.reduce((list, field) => {
			let foundField = db.entityValues
				.all((x) => x.entityFieldId === field.id && x.entityId === id)
				.sort(sortValuesByLatest)[0]

			return {
				...list,
				[field.key]: {
					id: foundField?.id || undefined,
					stringValue: foundField?.stringValue || '',
					dateValue: foundField?.dateValue || '',
					booleanValue: foundField?.booleanValue || false,
					numberValue: foundField?.numberValue || '',
				},
			}
		}, {}),
	})

	const formContext = useForm<FormSchema>({
		initialValues,
		onSubmit: (formData) => {
			if (!entityTypeId) return

			let newEntity = db.entities.save({ entityTypeId, ...(id ? { id } : {}) })

			Object.entries(formData.fields).forEach(([key, field]) => {
				let entityField = db.entityFields.find((x) => x.key === key)

				if (!entityField) return

				let save = false

				switch (entityField.type) {
					case 'string':
					case 'option':
						if (initialValues.fields[entityField.key].stringValue !== field.stringValue) {
							save = true
						}
						break
					case 'number':
						if (initialValues.fields[entityField.key].numberValue !== field.numberValue) {
							save = true
						}
						break
					case 'boolean':
						if (initialValues.fields[entityField.key].booleanValue !== field.booleanValue) {
							save = true
						}
						break
					case 'date':
						if (initialValues.fields[entityField.key].dateValue !== field.dateValue) {
							save = true
						}
						break
				}

				if (save || !field.id) {
					db.entityValues.save({
						...field,
						id: undefined,
						entityId: newEntity.id,
						entityFieldId: entityField?.id,
					})
				}
			})

			navigate(`${process.env.PUBLIC_URL}/${entityType?.key}`)
		},
	})

	const title = mode === 'create' ? `Create ${entityType?.plural}` : `Edit ${entityType?.plural}`

	return (
		<Form context={formContext}>
			<PageHeading
				pageTitle={title}
				title={
					<div className="flex items-center space-x-3">
						<Actions
							actions={{
								icon: <ArrowLeftIcon className="w-5 h-5 -mx-2" />,
								intent: 'borderless',
								rounded: 'full',
								to: `/${entityType?.key}`,
							}}
						/>
						<div>{title}</div>
					</div>
				}
			/>

			<PageContent>
				<Stack>
					<Card>
						<Stack>
							{fields.map((field) =>
								field.type === 'string' ? (
									<FormInput
										key={field.key}
										name={`fields.${field.key}.stringValue`}
										label={field.name}
									/>
								) : field.type === 'number' ? (
									<FormInput
										key={field.key}
										name={`fields.${field.key}.numberValue`}
										label={field.name}
										type="number"
									/>
								) : field.type === 'date' ? (
									<FormDate
										key={field.key}
										name={`fields.${field.key}.dateValue`}
										label={field.name}
									/>
								) : field.type === 'boolean' ? (
									<FormToggle
										key={field.key}
										name={`fields.${field.key}.booleanValue`}
										label={field.name}
									/>
								) : field.type === 'option' ? (
									<FormSelect
										key={field.key}
										name={`fields.${field.key}.stringValue`}
										label={field.name}
										options={field.options.map((x) => ({ label: x, value: x }))}
									/>
								) : null
							)}
						</Stack>
					</Card>
					<div className="flex items-center justify-end">
						<Actions
							actions={[
								{
									title: mode === 'create' ? 'Create' : 'Save',
									intent: 'primary',
									icon: <CheckIcon className="w-5 h-5" />,
									type: 'submit',
									disabled: formContext.isSubmitting,
								},
							]}
						/>
					</div>
				</Stack>
			</PageContent>
		</Form>
	)
}

export default EntityForm
